:root {
  --primary-color: #007bff;
  --primary-hover-color: #0056b3;
  --background-color: #f4f4f4;
  --container-bg-color: #fff;
  --text-color-dark: #333;
  --text-color-medium: #555;
  --border-color: #ddd;
  --light-border-color: #eee;
  --task-item-bg-color: #f9f9f9;
  --delete-btn-color: #dc3545;
  --delete-btn-hover-color: #c82333;
  --edit-btn-color: #ffc107;
  --edit-btn-hover-color: #e0a800;
  --save-btn-color: #28a745;
  --save-btn-hover-color: #218838;
  --white-color: white;
  --spacing-sm: 8px;
  --spacing-md: 10px;
  --spacing-lg: 15px;
  --spacing-xl: 25px;
  --padding-input: 12px;
  --padding-container: 30px;
  --padding-top-body: 50px;
  --border-radius-sm: 4px;
  --border-radius-md: 5px;
  --border-radius-lg: 8px;
  --box-shadow-light: 0 4px 8px #0000001a;
  --focus-shadow: 0 0 0 .2rem #007bff40;
  --font-family-sans: Arial, sans-serif;
  --font-size-base: 1rem;
  --font-size-lg: 1.1rem;
  --font-size-sm: .9rem;
}

body {
  font-family: var(--font-family-sans);
  background-color: var(--background-color);
  min-height: 100vh;
  padding-top: var(--padding-top-body);
  justify-content: center;
  align-items: flex-start;
  margin: 0;
  display: flex;
}

.container {
  background-color: var(--container-bg-color);
  padding: var(--padding-container);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--box-shadow-light);
  width: 100%;
  max-width: 500px;
}

h1 {
  text-align: center;
  color: var(--text-color-dark);
  margin-bottom: var(--spacing-xl);
}

.delete-btn, .edit-btn, .save-btn {
  padding: var(--spacing-sm) 12px;
  border-radius: var(--border-radius-sm);
  cursor: pointer;
  font-size: var(--font-size-sm);
  border: none;
  transition: background-color .2s;
}

.delete-btn {
  background-color: var(--delete-btn-color);
  color: var(--white-color);
}

.delete-btn:hover {
  background-color: var(--delete-btn-hover-color);
}

.edit-btn {
  background-color: var(--edit-btn-color);
  color: var(--text-color-dark);
}

.edit-btn:hover {
  background-color: var(--edit-btn-hover-color);
}

.save-btn {
  background-color: var(--save-btn-color);
  color: var(--white-color);
}

.save-btn:hover {
  background-color: var(--save-btn-hover-color);
}

.input-section {
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
  display: flex;
}

.input-section label {
  min-width: 80px;
  font-weight: bold;
}

.input-section input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  flex-grow: 1;
  padding: 8px;
}

.input-section button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 8px 15px;
}

.input-section button:hover {
  background-color: #0056b3;
}

.edit-input {
  padding: var(--spacing-sm);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-sm);
  font-size: var(--font-size-lg);
  margin-right: var(--spacing-md);
  flex-grow: 1;
}

#new-task-input {
  padding: var(--padding-input);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-md);
  font-size: var(--font-size-base);
  flex-grow: 1;
}

#new-task-input:focus {
  border-color: var(--primary-color);
  box-shadow: var(--focus-shadow);
  outline: none;
}

#add-task-btn {
  padding: var(--padding-input) 20px;
  background-color: var(--primary-color);
  color: var(--white-color);
  border-radius: var(--border-radius-md);
  cursor: pointer;
  font-size: var(--font-size-base);
  border: none;
  transition: background-color .2s;
}

#add-task-btn:hover {
  background-color: var(--primary-hover-color);
}

#task-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

#task-list li {
  background-color: var(--task-item-bg-color);
  border: 1px solid var(--light-border-color);
  padding: var(--spacing-lg);
  margin-bottom: var(--spacing-md);
  border-radius: var(--border-radius-md);
  word-wrap: break-word;
  word-break: break-all;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

#task-list li:last-child {
  margin-bottom: 0;
}

.task-text {
  font-size: var(--font-size-lg);
  color: var(--text-color-medium);
  margin-right: var(--spacing-md);
  flex-grow: 1;
}

.task-actions {
  gap: var(--spacing-sm);
  display: flex;
}

@media (width <= 600px) {
  .container {
    margin: 20px;
    padding: 20px;
  }

  #new-task-input, #add-task-btn {
    width: 100%;
  }

  #task-list li {
    align-items: flex-start;
    gap: var(--spacing-md);
    flex-direction: column;
  }

  .task-actions {
    justify-content: flex-end;
    width: 100%;
  }

  .task-text {
    width: 100%;
    margin-right: 0;
  }

  .edit-input {
    width: calc(100% - 16px);
    margin-right: 0;
  }
}
